import { useRef, useEffect } from "react";
import { useGSAP } from "@gsap/react";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";

gsap.registerPlugin(useGSAP, ScrollTrigger);

export default function Clouds() {
	const source = `${process.env.PUBLIC_URL}/images/drawnClouds.png`;
	const rootRef = useRef(null);

	useGSAP(
		() => {
			const clouds = gsap.utils.toArray(".cloud");
			clouds.forEach((cloud) => {
				gsap.to(cloud, {
					x: 150,
					scrollTrigger: {
						trigger: cloud,
						start: "top bottom",
						end: "bottom 20%",
						scrub: true,
						markers: true,
					},
				});
			});
		},
		{ scope: rootRef }
	);

	return (
		<div
			ref={rootRef}
			id="timeline"
			style={{
				width: "100%",
				height: "100vh",
				position: "relative",
				WebkitOverflowScrolling: "touch",
			}}
			
		>
			<div
				className="cloud"
				style={{
					position: "absolute",
					top: "36vh",
					left: "-60vw",
					width: "200px",
					height: "100px",
					willChange: "transform",
				}}
			>
				<img src={source} alt="" style={{ width: "200px", height: "100px" }} />
			</div>
			<div
				className="cloud"
				style={{
					willChange: "transform",
					position: "absolute",
					top: "7vh",
					width: "200px",
					height: "100px",
					left: "-50vw",
				}}
			>
				<img
					className="cloud"
					src={source}
					alt=""
					style={{ width: "200px", height: "100px" }}
				/>
			</div>
		</div>
	);
}
